.main-header {
  position: relative;
  max-height: 100px;

  .navbar {
    border-width: 0px;
    margin-bottom: 0px;
    border-radius: 0;
  }

  .navbar .nav > li > a > .label {
    position: absolute;
    top: 9px;
    right: 7px;
    text-align: center;
    font-size: 12px;
    padding: 2px 3px;
    line-height: .9;
  }

  >.logo {
    display: block;
    float: left;
    width: @main-header-logo-width;
    min-height: @main-header-height;
    line-height: 1.42857143;
  }


}


